.main-nav__list {
  list-style-type: none;
  padding: 0;
  margin: 0;

  display: flex;
  flex-direction: column;
}

.main-nav__item {
  width: 100%;
  min-height: 56px;
  box-shadow: inset 0 -1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  background-color: $blue;

  display: flex;
  justify-content: center;
  align-items: center;

  font-family: $font;
  font-size: 18px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 2.22;
  letter-spacing: normal;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;

  &--first {
    box-shadow: inset 0 -1px 0 rgba(1, 1, 1, 0.2);
    position: relative;

    &::after {
      content: "";
      width: 23px;
      height: 22px;
      background-image: url("../img/icon-cross.svg");
      background-repeat: no-repeat;
      position: absolute;
      top: 50%;
      right: 20px;
      margin-top: -11px;
      z-index: 2;
    }
  }

  &--last {
    box-shadow: inset 0 -20px 5px -20px rgba(1, 1, 1, 0.5), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }

  &:hover {
    background-color: #669ec0;
  }

  &:active {
    background-color: #5496bd;
    color: rgba(255, 255, 255, 0.3);
  }
}

.main-nav__logobox {
  padding: 0 20px;
  min-height: 56px;
  background-color: #ffffff;
  position: relative;

  display: flex;
  order: 5;
}

.main-nav__logo {
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -51px;
}

.main-nav__burger {
  background-color: transparent;
  border-style: none;
  padding: 0;
  margin: 0;
  margin-left: auto;
}

@media (min-width: 768px) {
  .main-nav {
    display: flex;
    justify-content: center;
  }

  .main-nav__list {
    display: flex;
    flex-direction: row;
  }

  .main-nav__item {
    width: 153px;
    font-family: $font;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.86;
    letter-spacing: normal;
    text-align: center;
    color: #000000;

    background-color: #ffffff;
    box-shadow: none;

    &:hover {
      background-color: #ffffff;
      color: $blue;
    }

    &:active {
      color: rgba(0, 0, 0, 0.3);
    }

    &--first {
      position: relative;

      &::after {
        content: "";
        width: 23px;
        height: 22px;
        background-image: none;
        position: absolute;
      }
    }

    &--active {
      color: #766357;

      &:hover {
        color: #604e43;
      }

      &:active {
        color: rgba(118, 99, 87, 0.3);
      }
    }
  }

  .main-nav__logo {
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: -51px;
    display: block;
  }

  .main-nav__logobox {
    order: initial;
    width: 156px;
    position: relative;
    display: flex;

    font-family: $font;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.86;
    letter-spacing: normal;
    text-align: center;
    color: #000000;

    background-color: #ffffff;
    box-shadow: none;
  }

  .main-nav__burger {
    display: none;
  }
}

@media (min-width: 1200px) {
  .main-nav__item {
    width: 239px;
  }

  .main-nav__logobox {
    width: 244px;
  }

  .main-nav__logo {
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: -94px;
  }

  .main-nav__wrap {
    display: flex;
  }
}
