.gallery__header {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}


.gallery__intro {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding-top: 18px;

  &--video {
    padding-top: 15px;
  }
}

.gallery__list {
  list-style-type: none;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.gallery__text {
  width: 273px;
  font-family: $font;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.71;
  letter-spacing: normal;
  text-align: center;
  color: #333333;
  text-transform: uppercase;

  &--title {
    font-size: 18px;
    font-weight: bold;
    line-height: 1.44;
    color: #000000;
  }

  &--last {
    margin-bottom: 27px;
  }

  &--video {
    margin-bottom: 14px;
  }
}

.gallery__box {
  margin: 0;
  padding-top: 22px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
}

.gallery__photobox {
  height: 280px;
  overflow: hidden;
  width: 280px;
}

.gallery__photo {
  width: 100%;
  height: auto;
  display: flex;
}

.gallery__info {
  font-family: $font;
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.71;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
  text-transform: uppercase;

  margin-bottom: 0;

  &--author {
    font-weight: normal;
    color: #333333;
    text-transform: capitalize;
    margin-top: 0;
  }
}

.gallery__likes {
  display: flex;
  align-items: center;
  margin-top: 5px;
  margin-bottom: 15px;

  font-family: $font;
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.71;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
}

.gallery__like {
  width: 23px;
  height: 21px;
  background-image: url("../img/icon-like0.svg");
  margin-right: 13px;
  opacity: 0.3;
  align-self: flex-start;
}

.gallery__count {
  padding-top: 5px;
}

.gallery__videobox {
  padding-bottom: 42px;
}

.gallery__video {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin-top: 23px;
}

.gallery__videowrap {
  width: 320px;
  height: 200px;
  overflow: hidden;
}

.gallery__timeline {
  min-width: 280px;
  height: 45px;
  background-color: rgba(0, 0, 0, 0.6);

  margin-top: -45px;
  padding-top: 2px;
  display: flex;

  &--v2 {
    display: none;
  }
}

.gallery__line {
  width: 235px;
  height: 3px;
  background-color: rgba(255, 255, 255, 0.2);
  margin: auto;
  position: relative;

  &::after {
    content: "";
    width: 9px;
    height: 9px;
    border-radius: 50%;
    background-color: #ffffff;
    opacity: 1;
    position: absolute;
    top: -3px;
    left: 35px;
    z-index: 2;
  }
}

.gallery__control {
  display: flex;
  align-items: center;

  width: 280px;
  min-height: 66px;
  padding-left: 25px;
  padding-right: 23px;
  background-color: $lightgray;
}

.gallery__btn {
  background-color: transparent;
  background-size: cover;
  background-position: center;
  border-style: none;
  padding: 0;

  &--replay {
    width: 16px;
    height: 20px;
    background-image: url("../img/icon-video-replay0.svg");
    margin-right: 133px;

    &:hover {
      background-image: url("../img/icon-video-replay-hover.svg");
    }

    &:active {
      background-image: url("../img/icon-video-replay-hover.svg");
      opacity: 0.3;
    }
  }

  &--sub {
    width: 32px;
    height: 20px;
    background-image: url("../img/icon-video-subtitles0.svg");
    margin-right: 31px;

    &:hover {
      background-image: url("../img/icon-video-subtitles-hover.svg");
    }

    &:active {
      background-image: url("../img/icon-video-subtitles-hover.svg");
      opacity: 0.3;
    }
  }

  &--full {
    width: 20px;
    height: 20px;
    background-image: url("../img/icon-video-fullscreen0.svg");

    &:hover {
      background-image: url("../img/icon-video-fullscreen-hover.svg");
    }

    &:active {
      background-image: url("../img/icon-video-fullscreen-hover.svg");
      opacity: 0.3;
    }
  }
}

@media (min-width: 768px) {
  .gallery__header {
    border-bottom: none;
  }

  .gallery__text {
    width: 428px;

    &--title {
      font-size: 30px;
      font-weight: bold;
      line-height: 0.87;
      color: #000000;

      margin-bottom: 15px;
    }

    &--last {
      margin-bottom: 72px;
    }

    &--video {
      margin-top: 50px;
      margin-bottom: 20px;
    }
  }

  .gallery__intro {
    display: flex;
    flex-direction: column;
    align-items: center;

    padding-top: 45px;

    &--video {
      padding-top: 15px;
    }
  }

  .gallery__list {
    display: flex;
    flex-wrap: wrap;
  }

  .gallery__item {
    flex-basis: 50%;
    display: flex;

    &--fullwidth {
      flex-basis: 100%;
    }
  }

  .gallery__box {
    padding-top: 0;
    border-bottom: none;

    &--fullwidth {
      margin-left: auto;
      margin-right: auto;
    }

    &--leftcol {
      margin-left: auto;
    }

    &--rightcol {
      margin-right: auto;
    }
  }

  .gallery__photobox {
    height: 267px;
    width: 324px;

    &--fullwidth {
      height: 400px;
      width: 648px;
    }
  }

  .gallery__description {
    padding: 1px;
    padding-left: 20px;
    position: relative;

    &--lightgray {
      background-color: $lightgray;
    }

    &--darkgray {
      background-color: $darkgray;
    }
  }

  .gallery__likes {
    position: absolute;
    left: 239px;
    top: 25px;

    &--fullwidth {
      left: 564px;
    }
  }

  .gallery__info {
    margin-top: 17px;
    padding-right: 80px;

    &--author {
      margin-top: 0;
      margin-bottom: 15px;
    }
  }

  .gallery__like {
    margin-right: 10px;
    background-image: url("../img/icon-like0.svg");
    opacity: 1;
  }

  .gallery__count {
    padding: 0;
  }

  .gallery__video {
    margin-top: 58px;
  }

  .gallery__videowrap {
    width: 648px;
    height: 500px;
  }

  .gallery__controlpanel {
    margin-top: -92px;
    position: relative;
  }

  .gallery__timeline {
    height: 45px;
    background-color: transparent;

    margin-top: -4px;
    margin-right: 42px;
    display: flex;
    position: absolute;
    top: 28px;
    left: 93px;
  }

  .gallery__line {
    width: 389px;
    height: 3px;
    background-color: rgba(255, 255, 255, 0.2);
    margin: auto;
    position: relative;

    &::after {
      content: "";
      width: 9px;
      height: 9px;
      border-radius: 50%;
      background-color: #ffffff;
      opacity: 1;
      position: absolute;
      top: -3px;
      left: 113px;
      z-index: 2;
    }
  }

  .gallery__control {
    min-width: 648px;
    min-height: 93px;
    padding-top: 3px;
    padding-left: 35px;
    padding-right: 41px;
    background-color: rgba(0, 0, 0, 0.6);
  }

  .gallery__videobox {
    padding-bottom: 45px;
  }

  .gallery__btn {
    &--replay {
      background-image: url("../img/icon-video-replay.svg");
      margin-right: 42px;

      &:hover {
        background-image: url("../img/icon-video-replay.svg");
        opacity: 0.6;
      }

      &:active {
        background-image: url("../img/icon-video-replay.svg");
        opacity: 0.3;
      }
    }

    &--sub {
      background-image: url("../img/icon-video-subtitles.svg");
      margin-left: auto;

      &:hover {
        background-image: url("../img/icon-video-subtitles.svg");
        opacity: 0.6;
      }

      &:active {
        background-image: url("../img/icon-video-subtitles.svg");
        opacity: 0.3;
      }
    }

    &--full {
      background-image: url("../img/icon-video-fullscreen.svg");

      &:hover {
        background-image: url("../img/icon-video-fullscreen.svg");
        opacity: 0.6;
      }

      &:active {
        background-image: url("../img/icon-video-fullscreen.svg");
        opacity: 0.3;
      }
    }
  }
}

@media (min-width: 1200px) {
  .gallery__intro {
    padding-top: 82px;

    &--video {
      padding-top: 22px;
    }
  }

  .gallery__text {
    &--title {
      margin-bottom: 17px;
    }
  }

  .gallery__list {
    padding-left: 0;
    padding-right: 0;
  }

  .gallery__box {
    margin-left: 0;
    margin-right: 0;
    width: 100%;
    position: relative;
  }

  .gallery__photobox {
    width: 400px;

    &--fullwidth {
      width: 1200px;
    }
  }

  .gallery__item {
    flex-basis: 33.333%;
    display: flex;

    &--fullwidth {
      flex-basis: 100%;
    }
  }

  .gallery__description {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.3);
  }

  .gallery__info {
    color: #ffffff;
    padding-right: 150px;
  }

  .gallery__likes {
    color: #ffffff;
    left: 315px;

    &--fullwidth {
      left: 1115px;
    }
  }

  .gallery__like {
    background-image: url("../img/icon-like.svg");
  }

  .gallery__videowrap {
    width: 800px;
    height: 500px;
  }

  .gallery__control {
    min-width: 800px;
  }

  .gallery__line {
    width: 540px;
  }

  .gallery__videobox {
    padding-bottom: 90px;
  }
}
