.page-footer__flexbox {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1px;
}

.page-footer__hashtag {
  font-family: $font;
  font-size: 21px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.24;
  letter-spacing: normal;
  text-align: center;
  color: #000000;
  text-transform: uppercase;

  padding-top: 10px;
}

.page-footer__social {
  margin-top: 15px;
}

.page-footer__text {
  font-family: $font;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.86;
  letter-spacing: normal;
  text-align: center;
  color: #000000;
  text-transform: uppercase;

  margin-top: 22px;
  min-height: 34px;
  display: flex;
  align-items: center;
}

.page-footer__htmlacademy {
  position: relative;
  width: 27px;
  height: 34px;
  padding-left: 41px;
  transition: 0;

  &::after {
    content: "";
    background-image: url("../img/logo-htmlacademy.svg");
    width: 27px;
    height: 34px;
    position: absolute;
    top: 0;
    right: 0;
    transition: 0;
  }

  &:hover {
    &::after {
      background-image: url("../img/logo-htmlacademy0.svg");
      transition: 0;
    }
  }

  &:active {
    &::after {
      background-image: url("../img/logo-htmlacademy.svg");
      opacity: 0.3;
    }
  }
}

@media (min-width: 768px) {
  .page-footer__flexbox {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 1px;
  }

  .page-footer__hashtag {
    padding-left: 15px;
    margin-top: 30px;
    padding-top: 0;
  }

  .page-footer__text {
    margin-right: 15px;
    margin-top: 27px;
  }

  .page-footer__social {
    margin-right: 8px;
    margin-top: 30px;
  }
}

@media (min-width: 1200px) {
  .page-footer__hashtag {
    padding-left: 63px;
    margin-top: 30px;
    padding-top: 0;
  }

  .page-footer__text {
    margin-right: 63px;
    margin-top: 27px;
  }

  .page-footer__social {
    margin-right: 8px;
    margin-top: 30px;
  }
}
